body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.people {
  grid-template-columns: repeat(2, 220px);
  justify-content: center;
  gap: 0 10px;
  padding: 30px 0;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 220px);
  }
}

.person {
  flex-direction: column;
  align-items: center;
  width: 220px;
  height: 287px;
  margin: 0 auto;
  transition: transform 1s;
  display: flex;
}

.person:hover {
  z-index: 1;
  background-color: #eee;
  transform: scale(1.2);
}

.person:hover .person__details__extra {
  display: block;
}

.person:hover .person__photo__img {
  border-radius: 60px;
}

.person__photo {
  width: 200px;
  height: 200px;
  display: flex;
  position: relative;
}

.person__photo__img {
  border-radius: 200px;
  width: 200px;
  height: 200px;
  margin-top: 10px;
  transition: all 1s;
}

.person__photo__rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  line-height: 32px;
  position: absolute;
  top: 94px;
  right: 10px;
}

.person__details {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.person__details__name {
  color: #2f4f4f;
  margin-top: 18px;
  font-size: 20px;
  font-weight: 700;
  line-height: 100%;
}

.person__details__extra {
  color: #000;
  margin-top: 9px;
  font-size: 18px;
  font-weight: 400;
  display: none;
}
